import { Component } from 'react'
import { Segment, Loader } from 'semantic-ui-react'

export default class Utile extends Component  {

  static loader = () => (
    <Segment>
      <Loader active />
    </Segment>
  )

    //í…ìŠ¤íŠ¸ ë¶„í•´
    static textDissect(text){
        return text.split('\n').map(
          v=>v.split('').map(
              v2=>this.getConstantVowel(v2)
          )
        );
    }

    //ìœ ë‹ˆì½”ë“œ ë¶„ë¦¬
    static getConstantVowel(kor) {
        const f = ['ã„±', 'ã„²', 'ã„´', 'ã„·', 'ã„¸', 'ã„¹', 'ã…',
                   'ã…‚', 'ã…ƒ', 'ã……', 'ã…†', 'ã…‡', 'ã…ˆ', 'ã…‰',
                   'ã…Š', 'ã…‹', 'ã…Œ', 'ã…', 'ã…Ž'];
        const s = ['ã…', 'ã…', 'ã…‘', 'ã…’', 'ã…“', 'ã…”', 'ã…•',
                   'ã…–', 'ã…—', 'ã…˜', 'ã…™', 'ã…š', 'ã…›', 'ã…œ',
                   'ã…', 'ã…ž', 'ã…Ÿ', 'ã… ', 'ã…¡', 'ã…¢', 'ã…£'];
        const t = ['', 'ã„±', 'ã„²', 'ã„³', 'ã„´', 'ã„µ', 'ã„¶',
                   'ã„·', 'ã„¹', 'ã„º', 'ã„»', 'ã„¼', 'ã„½', 'ã„¾',
                   'ã„¿', 'ã…€', 'ã…', 'ã…‚', 'ã…„', 'ã……', 'ã…†',
                   'ã…‡', 'ã…ˆ', 'ã…Š', 'ã…‹', 'ã…Œ', 'ã…', 'ã…Ž'];
    
        const ga = 44032;
        let uni = kor.charCodeAt(0);
    
        uni = uni - ga;
    
        let fn = parseInt(uni / 588);
        let sn = parseInt((uni - (fn * 588)) / 28);
        let tn = parseInt(uni % 28);

    
        let set = [kor];
        if(!(!f[fn] && !s[sn] && !t[tn])){
            set = [f[fn], kor]
            if(t[tn]){
                set = [f[fn],this.cjj2han(fn,sn), kor, ]
            }
            
        }

        return set;
    }

    //ìœ ë‹ˆì½”ë“œ í•©ì„±
    static cjj2han( cho = 0, jung = 0, jong = 0 ) {
        return String.fromCharCode( 0xAC00 + 21*28*cho + 28*jung + jong );
    }


}